    .chat {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 20px;
        height: 10vh;
        border-bottom: 1px solid rgba(211, 211, 211, 0.226);
    }
    
    a {
        text-decoration: none;
        color: inherit;
    }
    
    .chat__details {
        flex: 1;
    }
    
    .chat__details>p {
        color: gray;
    }
    
    .chat__timestamp {
        color: lightgrey;
    }
    
    .chat__image {
        margin-right: 20px;
    }